export default "import { Stack, SxProps } from \"@mui/material\";\nimport {\n  CartIcon,\n  NavBar,\n  NintendoLogo,\n  PlayStation4Logo,\n  PlayStation5Logo,\n  XboxLogo,\n} from \"../../../../../packages/ui/src\";\n\nimport Hero from \"../Hero/Hero.example\";\n\nconst sxInnerStack: SxProps = {\n  height: {\n    xs: \"1rem\",\n    md: \"unset\",\n  },\n  \"& svg\": { width: \"5rem\" },\n};\n\n// TODO: add Container for responsive\nexport default function Header() {\n  return (\n    <Stack sx={{ width: \"100%\" }} spacing={6}>\n      <NavBar cartComponent={<CartIcon />} onSearch={console.log} />\n      <Hero />\n      <Stack\n        sx={sxInnerStack}\n        direction=\"row\"\n        spacing={1}\n        justifyContent={\"space-evenly\"}\n      >\n        <PlayStation4Logo />\n        <PlayStation5Logo />\n        <NintendoLogo />\n        <XboxLogo />\n      </Stack>\n    </Stack>\n  );\n}\n"